
.select {
    min-width: 200px;
    background: transparent;
    color: #eee;
    font-size: 15px;
    border: none;
    border-radius: 0px;
    border-bottom: 1px solid #999;
    padding: 4px;
    -webkit-appearance: none;
    cursor: pointer;

    &:focus {
        outline: none;
    }
}